import { useState } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import './styles/index.css';

// Component imports
import Navigation from './components/Navigation/Navigation';
// import Hero from './components/Hero/Hero';
// import VideoGallery from './components/VideoGallery/VideoGallery';
// import About from './components/About/About';
// import Contact from './components/Contact/Contact';

function App() {
  const [currentSection, setCurrentSection] = useState('home');

  const handleSectionChange = (section: string) => {
    setCurrentSection(section);
  };

  return (
    <Router>
      <div className="App">
        <Navigation currentSection={currentSection} onSectionChange={handleSectionChange} />
        
        <main className="pt-16 lg:pt-20">
          {/* Hero section will be added in task 4 */}
          <section id="home" className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-50 to-gray-100">
            <div className="text-center">
              <h1 className="text-4xl md:text-6xl font-bold text-gray-900 mb-4">
                Video Portfolio Website
              </h1>
              <p className="text-xl text-gray-600 mb-8">
                Professional showcase for creative video work
              </p>
              <div className="space-x-4">
                <button className="btn-primary">
                  View Portfolio
                </button>
                <button className="btn-secondary">
                  Get in Touch
                </button>
              </div>
            </div>
          </section>

          {/* Video Gallery section will be added in task 6 */}
          <section id="portfolio" className="section-padding bg-white">
            <div className="container-custom">
              <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">
                Video Portfolio
              </h2>
              <p className="text-center text-gray-600">
                Gallery component will be implemented in task 6
              </p>
            </div>
          </section>

          {/* About section will be added in task 9 */}
          <section id="about" className="section-padding bg-gray-50">
            <div className="container-custom">
              <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">
                About
              </h2>
              <p className="text-center text-gray-600">
                About component will be implemented in task 9
              </p>
            </div>
          </section>

          {/* Contact section will be added in task 10 */}
          <section id="contact" className="section-padding bg-white">
            <div className="container-custom">
              <h2 className="text-3xl md:text-4xl font-bold text-center mb-12">
                Contact
              </h2>
              <p className="text-center text-gray-600">
                Contact component will be implemented in task 10
              </p>
            </div>
          </section>
        </main>
      </div>
    </Router>
  );
}

export default App;